import Vuex from "vuex";
import Vue from "vue";

import Constant from "../../Constant.js";
import http from "../../http-common.js";
Vue.use(Vuex);

const servicestore = {
  state: {
    services: [],
    service: {},
  },

  actions: {

    //   
    [Constant.GET_SERVICELIST]: (store) => {
      http
        .get("/bo-api/service/")
        .then((response) => {
          store.commit(Constant.GET_SERVICELIST, { services: response.data });
        })
        .catch((exp) => alert("getServiceList !!" + exp));
    },

    //  id   
    [Constant.GET_SERVICE]: (store, payload) => {
      http
        .get("/bo-api/service/" + payload.serviceId)
        .then((response) => {
          store.commit(Constant.GET_SERVICE, { service: response.data });
        })
        .catch((exp) => alert("getService ." + exp));
    },

    //  
    [Constant.REMOVE_SERVICE]: (store, payload) => {
      http
        .delete("/bo-api/service/" + payload.serviceId)
        .then(() => {
          alert(".");
          store.dispatch(Constant.GET_SERVICELIST);
        })
        .catch((exp) => alert("   ." + exp));
    },
  },

  mutations: {
    [Constant.GET_SERVICELIST]: (state, payload) => {
      state.services = payload.services;
    },
    [Constant.GET_SERVICE]: (state, payload) => {
      state.service = payload.service;
    },
  },

  modules: {},
};

export default servicestore;
