import Vuex from "vuex";
import Vue from "vue";

import Constant from "../../Constant.js";
import http from "../../http-common.js";
Vue.use(Vuex);

const termsstore = {
  state: {
    terms: [],
    term: {},
  },

  actions: {

    //   
    [Constant.GET_TERMLIST]: (store) => {
      http
        .get("/bo-api/terms/")
        .then((response) => {
          store.commit(Constant.GET_TERMLIST, { terms: response.data });
        })
        .catch((exp) => alert("getTermList !!" + exp));
    },

    //  id   
    [Constant.GET_TERM]: (store, payload) => {
      http
        .get("/bo-api/terms/" + payload.termId)
        .then((response) => {
          store.commit(Constant.GET_TERM, { term: response.data });
        })
        .catch((exp) => alert("getTerm ." + exp));
    },

    // 
    [Constant.MODIFY_TERM]: (store, payload) => {
      http
        .put(
          "/bo-api/terms/",
          {
            termsId : payload.termsId,
            name: payload.name,
            content: payload.content,
          }
        )
        .then(() => {
          store.dispatch(Constant.GET_TERMLIST);
        })
        .catch((exp) => alert("   ." + exp));
    },

    //  
    [Constant.REMOVE_TERM]: (store, payload) => {
      http
        .delete("/bo-api/terms/" + payload.termsId)
        .then(() => {
          alert(".");
          store.dispatch(Constant.GET_TERMLIST);
        })
        .catch((exp) => alert("   ." + exp));
    },
  },

  mutations: {
    [Constant.GET_TERMLIST]: (state, payload) => {
      state.terms = payload.terms;
    },
    [Constant.GET_TERM]: (state, payload) => {
      state.term = payload.term;
    },
  },

  modules: {},
};

export default termsstore;
