import Vuex from "vuex";
import Vue from "vue";

import Constant from "../../Constant.js";
import http from "../../http-common.js";
Vue.use(Vuex);

const devicecolorstore = {
  state: {
    devicecolors: [],
    devicecolor: {},
    //   
    deviceId: 0,
  },

  actions: {

    //   
    [Constant.GET_DEVICECOLORLIST]: (store, payload) => {
      http
        .get("/bo-api/deviceColor/device/" + payload.dId)
        .then((response) => {
          store.commit(Constant.GET_DEVICECOLORLIST, {
            devicecolors: response.data,
          });
        })
        .catch((exp) =>
          alert("getDeviceColorList !!" + exp)
        );
    },

    //  id   
    [Constant.GET_DEVICECOLOR]: (store, payload) => {
      http
        .get("/bo-api/deviceColor/" + payload.colorId)
        .then((response) => {
          store.commit(Constant.GET_DEVICECOLOR, {
            devicecolor: response.data,
          });
        })
        .catch((exp) => alert("getDeviceColor ." + exp));
    },

    //  
    [Constant.REMOVE_DEVICECOLOR]: (store, payload) => {
      http
        .delete("/bo-api/deviceColor/" + payload.colorId)
        .then(() => {
          alert(".");
          store.dispatch(Constant.GET_DEVICECOLORLIST, { dId: payload.dId });
        })
        .catch((exp) => alert("   ." + exp));
    },
  },

  mutations: {
    [Constant.GET_DEVICECOLORLIST]: (state, payload) => {
      state.devicecolors = payload.devicecolors;
    },
    [Constant.GET_DEVICECOLOR]: (state, payload) => {
      state.devicecolor = payload.devicecolor;
    },
    [Constant.MODIFY_DEVICEID]: (state, payload) => {
      state.deviceId = payload.deviceId;
    },
  },

  modules: {},
};

export default devicecolorstore;
